@use './variables' as v;
@use './mixins' as m;

h1,
h2,
h3 {
	font-family: v.$mainFont;
	margin: 0 0 v.$separation 0;
	text-transform: uppercase;
	text-align: center;
	line-height: 1.5;
}

h1 {
	font-size: 4rem;
}

h2 {
	font-size: 3.6rem;
	color: v.$mainColor;

	span {
		color: v.$secondColor;
		font-size: 1.8rem;
		display: block;
	}

	&::after {
		content: '';
		display: block;
		width: 10rem;
		height: 10rem;
		background-image: url(../images/cafe.svg);
		margin: 0 auto;
		background-size: 10rem;
	}
}

h3 {
	font-size: 3.2rem;
}

.heading-blanco {
	// heredando todas las propiedades de h2
	@extend h2;

	// reescribir lo necesario
	color: v.$white;

	span {
		color: v.$white;
	}

	&::after {
		display: none;
	}
}